@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*, ::before, ::after{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family:'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  overflow-x: hidden;
}

.container{
  text-align: center;
}
.search-box{
  width: 50vw;
}
@media (max-width: 556px){
  .search-box{
    width: 80vw;
  }
}
@media (min-width: 1200px){
  .search-box{
    width: 35vw;
  }
}
input{
  border: none;
  width: 100%;
  padding: 1em 1.2em;
  border-radius: 5px;
  margin-top: 1em;
  box-shadow: 0 2px 5px 0.5px #ccccccb0;
  color: #707070;
}
input:focus{
  outline: none;
}
input::-webkit-input-placeholder{
  color: #aaa;
}
input:-ms-input-placeholder{
  color: #aaa;
}
input::placeholder{
  color: #aaa;
}

.thin_text{
  font-weight: bold;
  font-size: 1em;
}
.temp_text{
  font-weight: lighter;
  font-size: 4em;
  margin: 0;
}

.info-section{
  margin-top: 3em;
}
